<template>
    <div class="wallet container">
        <div v-if="loading" class="wallet__loading-screen">
            <svg xmlns="http://www.w3.org/2000/svg" height="64px" viewBox="0 0 24 24" width="64px" fill="#000000"><path d="M0 0h24v24H0z" fill="none"/><path d="M12 4V1L8 5l4 4V6c3.31 0 6 2.69 6 6 0 1.01-.25 1.97-.7 2.8l1.46 1.46C19.54 15.03 20 13.57 20 12c0-4.42-3.58-8-8-8zm0 14c-3.31 0-6-2.69-6-6 0-1.01.25-1.97.7-2.8L5.24 7.74C4.46 8.97 4 10.43 4 12c0 4.42 3.58 8 8 8v3l4-4-4-4v3z"/></svg>
        </div>
        <div class="wallet__auth shadow-md" v-else-if="!addresses.length">
            <p class="headline-4">Connect Your wallet</p>
            <button class="wallet__btn" @click="connect">connect</button>
        </div>
        <div v-else>
            <div class="p-6 shadow-md min-h-screen" >
                <div class="headline-4 pt-4 pb-4 text-right">Balance  {{balance}}</div>
                <div class="headline-4 pb-4">Addresses</div>
                <div>
                    <div v-for='(address,index) in addresses' :key="address">{{index+1}}. {{address}}</div>
                </div>
                
            </div>
        </div>
    </div>
</template>
<script>
import Web3 from "web3";
import Web3Modal from "web3modal";

export default {
    name: 'Web3Modal',    
    data:()=>({
        addresses: [],
        balance: 0,
        loading: false
    }),
    methods:{
        connect: async function(){
            const web3Modal = new Web3Modal({
            network: "mainnet", // optional
            cacheProvider: false, // optional
            });
            this.loading = true
            const provider = await web3Modal.connect();
            const web3 = new Web3(provider);
            let account = await web3.eth.getAccounts()
            this.balance = await web3.eth.getBalance(account[0])
            this.addresses = account
            window.web3 = web3
            this.loading = false
        },
        

    }
}
</script>

